body {
    margin: 0;
  }
  
  nav {
    background-color: #30323d;
    padding: 15px 20px 15px 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    font-size: 1.1rem;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
  }
  
  .navMenu {
    flex-basis: 70%;
    max-width: 550px;
    display: flex;
    justify-content: space-between;
  }
  
  .navMenu span {
    padding: 10px;
    margin: 0 0 0 10px;
    cursor: pointer;
  }
  
  .navMenu span:hover {
    background-color: #484b5b;
  }
  
  .active {
    border-bottom: 2px solid #fff;
  }
  
  section {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3.5rem;
  }
  
  .home {
    background-color: #9c1de7;
  }
  
  .about {
    background-color: #28c7fa;
  }
  
  .services {
    background-color: #f85959;
  }
  
  .downloads {
    background-color: #107a8b;
  }
  
  .contact {
    background-color: #fd0054;
  }